export const mixin = {
  methods: {
    // 
    notify(title, type) {
      this.$notify({
        title: title,
        type: type
      });
    },
    // 
    getUrl(url) {
      return `${this.$store.state.HOST}/${url}`;
    },
    //
    changeSex(value) {
      if (value == 0) {
        return "";
      }
      if (value == 1) {
        return "";
      }
      if (value == 2) {
        return "";
      }
      if (value == 3) {
        return "";
      }
      return value;
    },
    //
    attachBirth(val) {
      return String(val).substring(0, 9);
    },
    //

    // 
    beforeAvatorUpload(file) {
      const isJPG =
        file.type === "image/jpeg" ||
        file.type === "image/jpg" ||
        file.type === "image/png";
      if (!isJPG) {
        this.$message.error("jpgjpegpng");
        return false;
      }
      // 
      const isLt2M = file.size / 1024 / 1024 < 5;
      if (!isLt2M) {
        this.$message.error("5MB");
        return false;
      }
      return true;
    },

    // 
    handleAvatorSuccess(res) {
      let _this = this;
      if (res.code === 1) {
        _this.getData();
        _this.$notify({
          title: "",
          type: "success"
        });
      } else {
        _this.$notify({
          title: "",
          type: "error"
        });
      }
    },

    // 
    handleDelete(id) {
      this.idx = id;
      this.delVisible = true;
    },

    //mutipleSelection
    handleSelectionChange(val) {
      this.multipleSelection = val;
    },

    //
    delAll() {
      for (let item of this.multipleSelection) {
        this.handleDelete(item.id);
        this.deleteRow();
      }
      this.multipleSelection = [];
    }
  }
};



